.title {
  font-size: 23px;
  font-weight: 700;
}
.item {
  background-color: #fff;
  border-radius: 15px;
  height: 40px;
  margin: 5px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  text-align: center;
  align-items: center;
  cursor: pointer;
  font-size: 14px;
  padding: 5px 20px;
}
.icon-small {
  width: 40px;
}
.daily-details-grid {
  display: grid;
  grid-template-columns: auto auto;
  row-gap: 0;
  column-gap: 15px;
  padding: 5px 15px;
}
.daily-details-grid-item {
  display: flex;
  height: 30px;
  justify-content: space-between;
  align-items: center;
}
.daily-details-grid-item label:first-child {
  color: #757575;
}
.daily-details-grid-item label:last-child {
  color: #212121;
}
